<template>
    <div id="app" class="game-wrapper">
        <div class="buttons-wrapper">
            <div class="button red" :class="{'active':buttonToAnimate==1}" @click="handleClick(1)"></div>
            <div
                class="button green"
                :class="{'active':buttonToAnimate==2}"
                @click="handleClick(2)"
            ></div>
            <div class="button blue" :class="{'active':buttonToAnimate==3}" @click="handleClick(3)"></div>
            <div
                class="button yellow"
                :class="{'active':buttonToAnimate==4}"
                @click="handleClick(4)"
            ></div>
            {{buttonDisabled}}
        </div>
        <div class="difficult-button-wrapper">
            <input
                type="radio"
                name="difficultLevel"
                value="1500"
                @click="difficultLevelClickHandler(1500)"
                checked
            /> easy
            <input
                type="radio"
                name="difficultLevel"
                value="1000"
                @click="difficultLevelClickHandler(1000)"
            /> normal
            <input
                type="radio"
                name="difficultLevel"
                value="450"
                @click="difficultLevelClickHandler(450)"
            /> hard
        </div>
        <p>Раунд: {{currentRound}}</p>
        <button @click.prevent="startGame(difficult)" class="start-game-btn">Start</button>
        <div>
            <audio src="/assets/sounds/lose.mp3"></audio>
            <audio src="/assets/sounds/fa.mp3"></audio>
            <audio src="/assets/sounds/lja.mp3"></audio>
            <audio src="/assets/sounds/mi.mp3"></audio>
            <audio src="/assets/sounds/sol.mp3"></audio>
        </div>
    </div>
</template>
<script>
/* eslint-disable */

export default {
    data() {
        return {
            series: new Array(50),
            buttonDisabled: false,
            currentRound: null,
            buttonToAnimate: 0,
            difficult: 1000,
            clickCounter: 0,
        };
    },

    methods: {
        startGame() {
            this.seriesInit();
            //this.series = [1, 2, 3, 4, 2, 3, 2, 1];
            this.currentRound = 1;
            this.clickCounter = 0;
            //this.animateSeries(this.series.slice(0, this.currentRound));
        },

        seriesInit() {
            this.series = new Array(50)
                .fill("")
                .map((elem) => Math.ceil(Math.random() * 4));
        },

        difficultLevelClickHandler(level) {
            this.startGame(level);
        },

        handleClick(button) {
            let audio = new Audio();
            audio.src = './src/assets/sounds/lose.mp3';
            audio.play();

            console.log(audio.src);

            if (!this.buttonDisabled) {
                if (button !== this.series[this.clickCounter]) {
                }

                this.lightUpWithTimer(button, 0, 500);

                if (this.clickCounter + 1 === this.currentRound) {
                    setTimeout(() => {
                        this.clickCounter = 0;
                        this.currentRound++;
                        this.animateSeries(
                            this.series.slice(0, this.currentRound)
                        );
                    }, 1000);
                } else {
                    this.clickCounter++;
                }
            }
        },

        lightUpWithTimer(button, timeout, duration) {
            setTimeout(() => {
                this.buttonToAnimate = button;
            }, timeout);

            setTimeout(() => {
                this.buttonToAnimate = 0;
            }, duration);
        },

        animateSeries(seriesToAnimate) {
            this.buttonDisabled = true;
            let timer = 0;

            seriesToAnimate.forEach((elem, ind) => {
                this.lightUpWithTimer(elem, timer, timer + this.difficult / 2);

                if (ind === seriesToAnimate.length - 1) {
                    this.buttonDisabled = false;
                }

                timer += this.difficult;
            });
        },
    },
};
</script>

<style src="./App.scss" lang="scss">
</style>
